import { TextInput, TextInputProps, ActionIcon, Grid, Container, useMantineTheme, SimpleGrid } from '@mantine/core';

import { IconSearch, IconArrowRight, IconArrowLeft } from '@tabler/icons';
import { useState } from 'react';
import Footer from './Footer';
import Nav from './Nav';
import { BadgeCard } from './BadgeCard';

function Lokalet() {

    const [value, setValue] = useState("");
    const [content, setContent] = useState([]);
    function HandleReFetch() {
        fetch("https://data.brreg.no/enhetsregisteret/api/enheter?forretningsadresse.poststed=" + value)
            .then(response => response.json())
            .then(result => {
                //console.log(result)
                setContent(result._embedded.enheter.map((company) => {
                    return <Grid.Col
                        gutter="md"
                        key={company.organisasjonsnummer}
                        span={4}>
                        <BadgeCard

                            image={"https://scontent.xx.fbcdn.net/v/t1.15752-9/319050516_587940730002478_1056009518990803037_n.png?stp=dst-png_p403x403&_nc_cat=103&ccb=1-7&_nc_sid=aee45a&_nc_ohc=bNr9pL9VCc0AX94W1ze&_nc_ad=z-m&_nc_cid=0&_nc_ht=scontent.xx&oh=03_AdTU6LzYDa7WxqLYCb-rRa-Txx9UAggONHWFRH1lZThzjg&oe=63F96459"}
                            url={company.hjemmeside}
                            title={company.navn}
                            country={company.forretningsadresse.poststed}
                            source={company.stiftelsesdato}

                            sector={company.organisasjonsform.beskrivelse}
                        />
                    </Grid.Col>
                }))
            })
            .catch(error => {
                console.log(error)
            });
    }


    function InputWithButton() {
        const theme = useMantineTheme();

        return (
            <TextInput
                icon={<IconSearch size={18} stroke={1.5} />}
                radius="xl"
                size="md"
                style={{paddingBottom: "20px"}}
                label="Vil du sjekke bed Byen din?"
                value={value}
                onChange={(event) => {
                    //console.log(event.target)
                    setValue(event.target.value)
                }}
                rightSection={
                    <ActionIcon onClick={() => {
                        HandleReFetch()
                    }}
                        size={32}
                        radius="xl"
                        color={theme.primaryColor}
                        variant="filled">
                        {theme.dir === 'ltr' ? (
                            <IconArrowRight size={18} stroke={1.5} />
                        ) : (
                            <IconArrowLeft size={18} stroke={1.5} />
                        )}
                    </ActionIcon>
                }
                placeholder="Sk en by ... eller noen land"
                rightSectionWidth={42}

            />
        );
    }

    return (
        <div>
            <Nav />
            <center>
                <h1>Se Bedrifter I Lokale Ditt.</h1>
            </center>
            <Container my="sm">
                {InputWithButton()}
                <SimpleGrid cols={1} spacing="md" >
                    <Grid breakpoints={[{ maxWidth: 'sm', cols: 1 }]}>
                        {content}
                    </Grid>
                </SimpleGrid>
                
            </Container>
            <Footer />
        </div>

    )
}

export default Lokalet;